<template>
    <div class="nation clearfix">
        <!-- 九宫格 -->
        <div class="nationItem" v-for="(item,i) in getNationality" :key="i">
            <div class="nationItemMain" @click="nameMore(item,i)">
                <div class="nationName">{{item.codename}}</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'nation',
    data() {
        return {
            nav: [],
            navWrap: [],
            activeIndex: -1,             //默认没选择
            className: [
                "zs", "wx", "jjx", "yx", "fx", "nx", "lsx", "lx", "glx", "jyx", "gx", "ysx"
            ],
            num: 0,                    //二级栏目渲染个数
            liClassName: false,         //二级栏目浮动
            lang:{},
            langType:"cn"
        }
    },
    mounted: function () {
        this.$nextTick(function () {
            this.toggleLang();
            var params = {
                firstkeyId: 'subject'
            }
            this.$store.commit("getNationality", params);
        });

    },
    watch:{
        "$route.fullPath":{
            handler(){
                this.toggleLang();
                var params = {
                    firstkeyId: 'subject'
                }
                this.$store.commit("getNationality", params);
            }
        }
    },
    computed: {
        //第一级
        getNationality: function () {
            return this.$store.state.Nationality;
        },
        //第二级
        getNationality_two: function () {
            return this.$store.state.NationalityTwo;
        }
    },
    methods: {
        // 鼠标进入
        ms: function (eleNodes, index, ops) {
            this.activeIndex = index;
            var id = ops.codekey;

            if (id != '' || id != null) {
                var params = {
                    firstkeyId: 'subject',
                    twokeyId: id
                }
                this.$store.commit("getNationality", params);
            }
        },
        // 鼠标移开
        ml: function (index) {
            this.activeIndex = -1;
        },
        nameMore: function (obj, i) {
            sessionStorage.removeItem("title");//清空缓存
            sessionStorage.removeItem("NationalityTwo");//清空缓存
            this.$router.push(
                {
                    name: this.langType!='cn'?this.langType+'natinalList':'natinalList',        //路径
                    params: {                   //传参
                        dataObj: obj
                    }
                }
            );
        },
        toggleLang(){
            const _this = this;
            this.commonMethods.selectLangType(function(langType){
                _this.lang = _this.langOption[langType];
                _this.langType = langType;
            })
        }
    }
}
</script>



